body {
  background-color: #F1EFEC;
  font-family: "Roboto", sans-serif;
  user-select: none;
}

.App {
  text-align: center;
  /*   border: 1px solid black; */
  width: 45%;
  height: 80vh;
  margin: 2% 26%;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  align-items: center;
  background-color: white;
  border-radius: 50px;
}

.title {
  font-size: 42px;
  font-weight: bold;
  color: #60B5FF;
  font-style: italic;
}

.span-location-data {
  font-size: 24px;
  font-family: "Special Gothic Condensed One", sans-serif;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.location-icon {
  width: 18px;
  height: 18px;
}

.search-bar {
  margin: 12px auto;
  width: 80%;
  margin-top: 40px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;

  input {
    width: 60%;
    height: 40px;
    font-size: 24px;
    border-radius: 20px;
    padding: 0 20px;
    /* box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px; */
    /* box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; */
    /* box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px; */
    box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
  }
}

.div-sky {
  /* margin-left: 20%; */
}

.btn {
  width: 20%;
  height: 45px;
  font-size: 24px;
  border-radius: 20px;
  margin-left: 10px;
  box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
}

.btn {
  color: #00b4d8;
  text-transform: uppercase;
  text-decoration: none;
  border: 2px solid #00b4d8;
  padding: 10px 20px;
  font-size: 17px;
  cursor: pointer;
  font-weight: bold;
  background: transparent;
  position: relative;
  transition: all 0.8s;
  overflow: hidden;
}

.btn:hover {
  background-color: black;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
  color: white;
}

.btn::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 0%;
  top: 0;
  left: -40px;
  transform: skewX(45deg);
  background-color: #00b4d8;
  z-index: -1;
  transition: all 1s;
}

.btn:hover::before {
  width: 160%;
}

.btn:disabled{
  cursor: auto;
  border: 2px solid black;
  color: black;
  background-color: lightgray;
  opacity: 0.8;
  box-shadow: none;
  opacity: 0.4;
}

.btn:disabled::before {
  transform: none;
  left: -2;
  width: 0;
  transition: none;
}

.card {
  margin: 20px auto;
  width: 95%;
  height: 65%;
  /*   border: 1px solid black; */
  border-radius: 20px;
  padding: 15px;
  background-color: white;
  /* box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px; */
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.div-city-details {
  width: 100%;
  height: 10%;
  /* border: 1px solid black; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3px;
}

.span-details {
  letter-spacing: 0.5px;
}

.left-section {
  margin-top: 2px;
  /* border: 1px solid black; */
  width: 59%;
  height: 88%;
  float: left;
  /*   display: flex;
  justify-content: space-around;
  align-items: center; */
}

.abc {
  display: flex;
  justify-content: left;
  align-items: center;
  margin-top: 20%;
  height: 40%;
}

.right-section {
  margin-top: 5%;
  /* border: 1px solid black; */
  width: 40%;
  height: 88%;
  float: right;
}

.icon {
  float: left;
  margin: 20% 0;
  width: 100px;
  height: 100px;
}

.current-temp {
  font-family: "Oswald", sans-serif;
  float: left;
  font-size: 84px;
  font-weight: 600;
  /* margin: 20% 5%; */
}

.sky-details {
  font-family: "Oswald", sans-serif;
  position: relative;
  margin-top: 34%;
  font-size: 28px;
  left: -25%;
}

.float-left {
  float: left;
  margin-left: 5px;
  padding: 10px;
}

.float-right {
  float: right;
  margin-right: 5px;
  padding: 10px;
  font-weight: bold;
}

.right-section-item {
  margin: 0 auto;
  width: 100%;
  height: 8%;
}

hr {
  width: 100%;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.5);
  text-align: center;
}

.loading-icon {
  width: 200px;
  height: 200px;
}

.error-handler {
  margin-top: 2%;
  width: 45%;
  height: 5%;
  background-color: #FF033E;
  border-radius: 50px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

.alert-icon {
  width: 20px;
  height: 20px;
  margin: 0px 8px 3px 0px;
}

@media screen and (max-width: 600px) {
  .card {
    margin: 20px auto;
    width: 90%;
    height: 80%;
  }

  .App {
    width: 90%;
    height: 80vh;
    margin: 2% auto;
  }

  .header {
    padding: 4px 8px;
  }

  .title {
    font-size: 22px;
  }

  .span-location-data {
    font-size: 16px;
  }

  .location-icon {
    width: 12px;
    height: 12px;
  }

  .icon {
    width: 80px;
    height: 80px;
    margin-top: 31%;
  }

  .current-temp {
    font-size: 32px;
    width: 50%;
  }

  .abc {
    justify-content: center;
    width: 111%;
    margin-left: -25px;
    margin-top: 10%;
  }

  .div-sky {
    margin-left: 40%;
  }

  .left-section {
    margin-top: 2px;
    width: 100%;
    height: 20%
  }

  .right-section {
    margin-top: 26px;
    width: 100%;
    height: 70%;
    float: left;
  }

  .search-bar {
    margin: 20px 0;
    width: 100%;

    input {
      font-size: 18px;
      height: 30px;
      width: 54%;
    }
  }

  .btn {
    font-size: 18px;
    width: 30%;
    height: 34px;
    padding: 0;
  }

  .span-details {
    font-size: 12px;
    text-align: right;
  }

  .div-city-details> :first-child {
    font-size: 16px;
    text-align: left;
  }

  .button-78 {
    padding: 0 26px;
    min-height: 30px;
    height: 35px;
  }

  .error-handler {
      padding: 5px;
      width: 73%;
      height: 15%;
      background-color: #FF033E;
      border-radius: 20px;
      position: absolute;
      box-shadow: rgba(0, 0, 0, 0.56) -40px 48px 110px 45px;
      top: 40%;
      left: 13%;
      word-break: break-word;
  }
}

@media screen and (min-width: 1600px) and (max-width: 2400px) {
  .App {
    margin: 3% 26%;
  }

  .card {
    margin: 6% auto;
    height: 60%;
  }

  .search-bar {
    margin-top: 7%;
  }

  .right-section {
    margin-top: 7%;
  }

  .error-handler {
    margin-top: -2%;
  }
}